@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300;400;500;600;700&display=swap');

:root{
    --mainTextColor-light:#000;
    --secondaryTextColor-light:rgb(51 51 51);
    --mainLinkColor-light:#0da2b8;
    --mainBorderColor-light:rgb(218, 218, 218);
    --mainBgColor-light:rgb(249, 250,251);

    --mainTextColor-dark:#fff;
    --secondaryTextColor-dark:#adb0b1;
    --mainLinkColor-dark:rgb(30, 190,214);
    --mainBorderColor-dark:#2b3031;
    --mainBgColor-dark:#131415;
    
    --mainTextColor:var(--mainTextColor-light);
    --secondaryTextColor:var(--secondaryTextColor-li--mainTextColor-light);
    --mainLinkColor:var(--mainLinkColor-li--mainTextColor-light);
    --mainBorderColor:var(--mainBorderColor-li--mainTextColor-light);
    --mainBgColor:var(--mainBgColor-li--mainTextColor-light);

}

/* Each state */
a { color:rgb(30, 190,214) }
a:visited { text-decoration: none; color:rgb(30, 190,214); }
a:hover { text-decoration: none; color:#adb0b1; }
a:focus { text-decoration: none; color:#adb0b1; }
a:hover, a:active { text-decoration: none; color:#adb0b1 }

*{
    font-family: 'Readex Pro';
    line-height: 1.5em;
    box-sizing: border-box;
    
}


p, span, li{
    font-size: 1em;
}

a{
    text-decoration: none;
    font-weight: 500;
}

li{
    line-height: 1.9em;
}

#container--main{
    max-width: 700px;
    margin: 0 auto;
    padding: 1em;
}

.section--page{
    padding-top: 1em;
    padding-bottom: 1em;
}

#wrapper--hero{
    display: flex;
    align-items: center;
    gap: 4em;
}

#bio, a{
    font-weight: 300;
}

#user-name{
    font-size: 48px;
    line-height: 1em;
}

#profile-pic{
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
}

